<template>
  <div class="changepass">
    <div style="flex-direction: column;" class="sct login">
      <h3 id="msgerror">{{msg}}</h3>
      <form method="POST">
        <h3>Change Password</h3>
        <input v-model="user" type="text" id="chanuser" name="username" placeholder="Username" required>
        <input v-model="pass" type="password" id="chanpass" name="password" placeholder="Password" required>
        <input v-model="reppass" type="password" id="reppass" name="reppassword" placeholder="Confirm Password" required>
        
        <input @click="change($event)" type="submit" id="gochange" name="changepassword" value="Change">
      </form>
    </div>
  </div>
</template>

<script>
  import ajaxrequest from '../api/ajax.js'
  import getParameterByName from '../api/getparamurl.js'
  export default {
    name: 'changepass',
    data() {
      return {
        user: '',
        pass: '',
        reppass: '',
        msg:''
        
      }
    },
    methods: {
        change: function ($event) {
            var changego = getParameterByName('changego');
            if(changego=="tochangepasssword"){
                event.preventDefault()
                const self = this
                self.msg=''
                console.log(self.user+'-'+self.pass+'-'+self.reppass)
                if(self.user=='' || self.pass=='' || self.reppass==''){
                    self.msg="Please Enter Credentials"
                }else if(self.pass!=self.reppass) {
                    self.msg="Passwords must match"
                }else {
                    console.log("envio")
                    ajaxrequest('get','http://localhost/readsea/public/changepassword?user='+self.user+'&pass='+self.pass)
                    .then (function (data){
                        if(data.status=="user_error"){
                            self.msg="Incorrect Username"
                        }else if(data.status=="ok"){
                            self.msg="Password was changed correctly"
                            self.user=''
                            self.pass=''
                            self.reppass=''
                        }
                    })
                }
            }
      },
    },
    beforeMount() {
      console.log("Change Password")
    }

  }

</script>

<style>
.changepass {
    margin-top: 90px;
}
</style>
